body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

h1, h2 {
  margin: 20px 0;
}

hr {
  border: none;
  border-top: 1px solid #ccc;
  margin: 20px 0;
}

.list-group-item {
  cursor: pointer;
}

.list-group-item.active {
  background-color: #007bff;
  color: #fff;
}

@media (max-width: 576px) {
  h1, h2 {
    margin: 10px 0;
  }

  hr {
    margin: 10px 0;
  }
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  
}

.div-parent {
  display: flex;

}

.div-child {
  display: flex;
  flex-basis: 70%;
  flex-direction: column;
  margin-left: 2rem;
  flex-wrap: wrap;
 
  
}

.div-child2 {
  display: flex;
  flex-basis: 30%;
  margin-left: 4rem;
  
}


.acordion {
  /* background-color: rgb(104, 186, 225); */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.search {
  box-shadow: 0 4px 8px 0 rgba(15, 25, 136, 0.2), 0 6px 20px 0 rgba(72, 213, 218, 0.19);
  padding: 1rem;
}

.acordion-body {
  padding: 1rem;
  margin: 1rem;
  border-radius: 15px;
  background-color: rgb(167, 217, 239);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media (max-width: 576px) {
  .container {
    padding: 10px;
  }
}

